import foot from './common/footer/footer';
import heade from './common/header/header';

export default {
  name: 'App',
  components:{
      foot,
      heade,
  },
  computed:{
    keepAlive(){
      return this.$store.state.keepAlive
    },
    logined(){
      return this.$store.state.logined
    }
  },
}



