
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import orderFromVue from '../../pages/enter/children/orderFrom.vue';

export default {
name:'foot',
components:{
Tabbar,
TabbarItem,
},
data(){
return{
selected:0
}
},
mounted(){
let _this = this
_this.bus.$on('routerChange',function(msg){
if(msg=="/"){
_this.$router.push({path:'/'})
_this.selected=0

}else if(msg =="/orderFrom"){
_this.$router.push({path:'/orderFrom'})
_this.selected=0
}else if(msg=="/changeOrder"){
_this.$router.push({path:'/changeOrder'})
_this.selected=1
}else if(msg == "/orderDetail"){
_this.$router.push({path:'/orderDetail'})
_this.selected=1
}else if(msg == "/searc"){
_this.$router.push({path:'/orderDetail'})
_this.selected=1
}
})
},
watch:{

selected(val,oldVal){
if(val==1){
this.$router.push({path:'/searc'})

}else if(val == 0){
this.$router.push({path:'/'})

}else if(val == 2){
this.$router.push({path:'/mine'})
}
}


},
}



